.App {
  text-align: center;
}
.mainpanel {
  display: flex;
  align-items: flex-start;
}
.leftPanel {
  display: inline-block;
  width: 40vw;
  height: 100vh;
  overflow: scroll;
}
.rightPanel {
  height: 100vh;
  display: inline-block;
  width: 60vw;
  margin-left: 12px;
  overflow: scroll;
}
.modal-item {
  width: 200px;
  margin-bottom: 10px;
}
.modal-item-input {
  width: 50%;
  display: inline-block;
  position: absolute;
  right: 10px;
}
.leftPanel-header-btn {
  display: flex;
  justify-content: space-around;

}
.leftPanel-header, .leftPanel-table {
  padding: 20px;
